.shop-banners {
  padding-top: 75px;
}

.shop-banners__container {
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  padding: 160px 20px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-dark);
  color: var(--color-white);
}

.shop-banner::before {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.shop-banner__title {
  position: relative;
  z-index: 1;
  margin: 0;
  margin-bottom: 20px;
  font-size: 48px;
  font-weight: 500;
}

.shop-banner__text {
  position: relative;
  z-index: 1;
  font-size: 15px;
}

.shop-banner--man {
  background-image: url("../../img/banner1.jpg");
}

.shop-banner--woman {
  background-image: url("../../img/banner2.jpg");
}
